import { Route } from 'react-router-dom';
import * as React from 'react';
import Loadable from '@loadable/component';

// 
export default [
  <Route 
    key="calculator" 
    exact={true} 
    path="/calculator" 
    component={Loadable(() => import('@/components/Calculator'))} 
  />
]



